html {
  height: 100%;
}

.page__body {
  margin: 0;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
}

.page__body-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main-container {
  flex: 1 1 auto;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Lato-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  background-color: #68b738;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  padding: 11px 20px;
  text-transform: uppercase;
  border: 0;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: #ffffff4d;
}
.button:disabled {
  background-color: #5eaa2f;
  color: #ffffff4d;
}

.title {
  font-size: 36px;
  line-height: 40px;
  margin: 0 0 40px 0;
}

@media (min-width: 768px) {
