}
.main-header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 21px;
  top: 0;
  background-color: #ffffff;
  left: 0;
  right: 0;
  height: 66px;
  z-index: 4;
  position: relative;
}

.main-header__menu img:hover {
