.menu__button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 30px;
  cursor: pointer;
  transition: 0.4s;
}

@media (min-width: 768px) {
  .menu__button {
    display: none;
  }
}
.menu__icon {
  display: block;
  position: relative;
  background-color: #000000;
  width: 90%;
  height: 2px;
  transition: 0.4s;
}
.menu__icon::after, .menu__icon::before {
  content: "";
  display: block;
  position: absolute;
  background-color: #000000;
  width: 100%;
  height: 2px;
  transition: 0.4s;
}

.menu__icon::after {
  top: 8px;
}

.menu__icon::before {
  top: -8px;
}

.menu__container {
  position: absolute;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 65px;
  left: 0;
  right: 0;
  z-index: 4;
  height: 0;
  opacity: 1;
  transition: 0.5s;
  overflow: hidden;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .menu__container {
    position: static;
    top: auto;
    left: auto;
    right: auto;
    overflow: visible;
  }
}
@media (min-width: 1440px) {
  .main-header--additional .menu__container {
    margin-left: 543px;
  }
}
.menu__list {
  transition: 0.5s;
  list-style: none;
  padding-left: 0;
  width: 100%;
  margin: 0;
}

@media (min-width: 768px) {
  .menu__item--current {
    position: relative;
  }
  .menu__item--current::before {
    position: absolute;
    content: "";
    left: 17px;
    right: 17px;
    bottom: -9px;
    height: 2px;
    background: #68b738;
    z-index: 2;
  }
}
@media (min-width: 1440px) {
  .menu__item--current::before {
    bottom: -13px;
  }
}
@media (min-width: 1440px) {
  .menu-item--main-current::before {
    background: #ffffff;
  }
}
@media (min-width: 768px) {
  .menu__list {
    display: flex;
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .menu__list {
    background-color: transparent;
  }
}
.menu__item {
