  }
}
.menu__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  width: 100%;
}

@media (min-width: 768px) {
  .menu__link {
    padding: 19px 17px 18px 17px;
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
